/**
 *
 * @param {Boolean} isOpen 是否关闭loading
 */
export const onLoading = (that, isOpen) => {
    const loading = that.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(255, 255, 255, 0.9)',
        customClass: 'loading',
    });
    isOpen && loading.close();
};

export const setPublicSrc = (url) => {
    const BASE_URL =
        process.env.NODE_ENV === 'production' ? '/ThreejsDemos' : '';
    return BASE_URL + url;
};
